"use client";

import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import { useRouter } from "next/navigation";
import { AnimatePresence, motion } from "framer-motion";

import type { SearchResult } from "@/lib/search";
import { searchContent } from "@/lib/search";
import { useFeatureFlag } from "@/context/feature-flags";
import { designTokens } from "@/lib/design/tokens";

interface CommandPaletteProps {
  open: boolean;
  onClose: () => void;
}

type CommandItem = SearchResult & { categoryLabel: string };

const defaultCommands: CommandItem[] = [
  {
    id: "nav-news",
    title: "Browse AI newsroom",
    description: "Editorial updates, digest, and curated launch coverage.",
    href: "/news",
    score: 1,
    badge: "News",
    categoryLabel: "Navigation",
  },
  {
    id: "nav-comparisons",
    title: "Compare leading LLMs",
    description: "Benchmark radar, pricing estimators, and sourcing trails.",
    href: "/comparisons",
    score: 1,
    badge: "Comparisons",
    categoryLabel: "Navigation",
  },
  {
    id: "nav-learn",
    title: "Open learning tracks",
    description: "Curated modules with capstones and badge incentives.",
    href: "/learn",
    score: 1,
    badge: "Learn",
    categoryLabel: "Navigation",
  },
  {
    id: "nav-models",
    title: "Explore model directory",
    description: "Filters for provider, licensing, and deployment posture.",
    href: "/models",
    score: 1,
    badge: "Models",
    categoryLabel: "Navigation",
  },
];

export function CommandPalette({ open, onClose }: CommandPaletteProps) {
  const enabled = useFeatureFlag("commandPalette");
  const router = useRouter();
  const inputRef = useRef<HTMLInputElement | null>(null);
  const [query, setQuery] = useState("");
  const [activeIndex, setActiveIndex] = useState(0);

  const results = useMemo<CommandItem[]>(() => {
    if (!enabled) {
      return [];
    }
    if (!query.trim()) {
      return defaultCommands;
    }

    const search = searchContent(query, 6);
    return search.groups.flatMap((group) =>
      group.results.slice(0, 5).map((result) => ({
        ...result,
        categoryLabel: group.label,
      })),
    );
  }, [enabled, query]);

  useEffect(() => {
    if (open) {
      setQuery("");
      setActiveIndex(0);
      const id = window.setTimeout(() => {
        inputRef.current?.focus();
      }, 10);
      return () => window.clearTimeout(id);
    }
    return undefined;
  }, [open]);

  useEffect(() => {
    if (!open) return undefined;
    const handleKey = (event: KeyboardEvent) => {
      if (event.key === "Escape") {
        event.preventDefault();
        onClose();
      }
      if (!results.length) {
        return;
      }
      if (event.key === "ArrowDown") {
        event.preventDefault();
        setActiveIndex((index) => Math.min(index + 1, results.length - 1));
      } else if (event.key === "ArrowUp") {
        event.preventDefault();
        setActiveIndex((index) => Math.max(index - 1, 0));
      } else if (event.key === "Enter") {
        event.preventDefault();
        const selected = results[activeIndex];
        if (selected) {
          handleSelect(selected);
        }
      }
    };
    window.addEventListener("keydown", handleKey);
    return () => window.removeEventListener("keydown", handleKey);
  }, [open, results, activeIndex, onClose, router]);

  const handleSelect = useCallback(
    (item: CommandItem) => {
      onClose();
      router.push(item.href);
    },
    [onClose, router],
  );

  if (!enabled) {
    return null;
  }

  return (
    <AnimatePresence>
      {open ? (
        <motion.div
          className="fixed inset-0 z-[80] flex items-start justify-center bg-black/60 px-4 py-20 backdrop-blur-xl"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          transition={{
            duration: designTokens.motion.duration.page / 1000,
            ease: designTokens.motion.easing.entrance,
          }}
          onMouseDown={(event) => {
            if (event.target === event.currentTarget) {
              onClose();
            }
          }}
        >
          <motion.div
            className="w-full max-w-2xl overflow-hidden rounded-[var(--radius-xxl)] border border-[var(--border-soft)]/60 bg-[var(--color-surface)] shadow-[var(--shadow-depth)]"
            initial={{ scale: 0.96, opacity: 0, y: 24 }}
            animate={{ scale: 1, opacity: 1, y: 0 }}
            exit={{ scale: 0.98, opacity: 0, y: 16, transition: { duration: designTokens.motion.duration.fast / 1000, ease: designTokens.motion.easing.exit } }}
            transition={{
              duration: designTokens.motion.duration.page / 1000,
              ease: designTokens.motion.easing.entrance,
            }}
            role="dialog"
            aria-modal="true"
            aria-label="Command palette"
          >
            <div className="flex items-center gap-3 border-b border-[var(--border-soft)]/60 px-6 py-4">
              <span className="text-xs font-semibold uppercase tracking-[0.4em] text-[var(--color-muted)]/80">Search</span>
              <input
                ref={inputRef}
                data-command-input
                value={query}
                onChange={(event) => {
                  setQuery(event.target.value);
                  setActiveIndex(0);
                }}
                placeholder="Jump to news, models, tracks, or benchmarks"
                className="flex-1 bg-transparent text-base text-[var(--color-foreground)] placeholder:text-[var(--color-muted)] focus:outline-none"
              />
              <kbd className="rounded-md border border-[var(--border-soft)]/60 bg-white/10 px-2 py-1 text-[10px] uppercase tracking-[0.3em] text-[var(--color-foreground)]/70">
                Esc
              </kbd>
            </div>

            <div className="max-h-[420px] overflow-y-auto px-2 py-4">
              {results.length ? (
                <ul className="flex flex-col gap-2">
                  {results.map((item, index) => {
                    const isActive = index === activeIndex;
                    return (
                      <li key={`${item.categoryLabel}-${item.id}`}>
                        <button
                          type="button"
                          onClick={() => handleSelect(item)}
                          className={`flex w-full flex-col gap-1 rounded-[var(--radius-xl)] border px-4 py-3 text-left transition ${
                            isActive
                              ? "border-[var(--color-accent-secondary)]/60 bg-[var(--color-accent-secondary)]/15 text-[var(--color-foreground)]"
                              : "border-[var(--border-soft)]/50 text-[var(--color-foreground)]/90 hover:border-[var(--color-accent-secondary)]/40 hover:bg-[var(--color-surface)]/70"
                          }`}
                        >
                          <div className="flex items-center justify-between gap-3 text-xs uppercase tracking-[0.32em] text-[var(--color-muted)]/80">
                            <span>{item.categoryLabel}</span>
                            {item.meta ? <span>{item.meta}</span> : null}
                          </div>
                          <span className="text-sm font-semibold text-[var(--color-foreground)]">{item.title}</span>
                          <p className="text-sm leading-relaxed text-[var(--color-muted)]">{item.description}</p>
                        </button>
                      </li>
                    );
                  })}
                </ul>
              ) : (
                <div className="rounded-[var(--radius-xl)] border border-dashed border-[var(--border-soft)]/50 bg-[var(--color-surface)]/50 px-4 py-10 text-center text-sm text-[var(--color-muted)]">
                  No matches yet. Try searching for a model, benchmark, or track.
                </div>
              )}
            </div>
          </motion.div>
        </motion.div>
      ) : null}
    </AnimatePresence>
  );
}
